a-nav {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  position: fixed;
  top: 0;
  background-color: white;
  width: 100%;
  z-index: 30;
  min-height: 3.25rem;
  box-shadow: 0 2px 0 0 #f5f5f5;
}
a-nav > .nav-left,
a-nav > .nav-right {
  display: none;
}
a-nav > .nav-left.active,
a-nav > .nav-right.active {
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 768px) {
  a-nav.active > .nav-left,
  a-nav.active > .nav-right {
    display: flex;
    flex-direction: column;
  }
}
a-nav > .nav-home {
  align-self: flex-start;
  height: 3.25rem;
}
a-nav > .nav-toggle {
  position: absolute;
  top: 0rem;
  right: 0rem;
  height: 3.25rem;
  width: 3.25rem;
  cursor: pointer;
}
a-nav > .nav-toggle:hover {
  background-color: #fafafa;
  color: #3273dc;
}
a-nav > .nav-toggle > span {
  background-color: currentColor;
  display: block;
  height: 1px;
  left: calc(50% - 8px);
  position: absolute;
  -webkit-transform-origin: center;
  transform-origin: center;
  transition-duration: 86ms;
  transition-property: background-color, opacity, -webkit-transform;
  transition-property: background-color, opacity, transform;
  transition-property: background-color, opacity, transform, -webkit-transform;
  transition-timing-function: ease-out;
  width: 16px;
}
a-nav > .nav-toggle > span:nth-child(1) {
  top: calc(50% - 6px);
}
a-nav > .nav-toggle > span:nth-child(2) {
  top: calc(50% - 1px);
}
a-nav > .nav-toggle > span:nth-child(3) {
  top: calc(50% + 4px);
}
a-nav.active > .nav-toggle > span:nth-child(1) {
  transform: translateY(5px) rotate(45deg);
}
a-nav.active > .nav-toggle > span:nth-child(2) {
  opacity: 0;
}
a-nav.active > .nav-toggle > span:nth-child(3) {
  transform: translateY(-5px) rotate(-45deg);
}
@media screen and (min-width: 769px) {
  a-nav {
    flex-direction: row;
  }
  a-nav::before {
    content: '';
    flex: 2 1 auto;
  }
  a-nav::after {
    content: '';
    flex: 1 1 auto;
  }
  a-nav > .nav-left {
    display: flex;
    flex-direction: row;
    flex: 6 1 auto;
  }
  a-nav > .nav-right {
    display: flex;
    flex-direction: row;
    flex: 1 1 auto;
  }
  a-nav > .nav-home {
    align-self: stretch;
  }
  a-nav > .nav-toggle {
    display: none;
  }
}
a-nav a {
  cursor: pointer;
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
  align-items: center;
  line-height: 1.5;
  padding: 0.5rem 0.75rem;
  color: #4a4a4a;
  text-decoration: none;
}
a-nav a.active {
  color: #209cee;
}
a-nav a:hover {
  background-color: #fafafa;
  color: #3273dc;
}
a-nav a img {
  max-height: 1.75rem;
  height: auto;
  max-width: 100%;
}
